import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import {
  Typography,
  Button,
  Card,
  CardMedia,
  IconButton,
} from '@material-ui/core';
import Message from '@material-ui/icons/PermPhoneMsgRounded';
import { green } from '@material-ui/core/colors';
import Back from '@material-ui/icons/ArrowBackRounded';
import { Link, Redirect } from 'react-router-dom';
import { isMobile } from 'react-device-detect';

const useStyles = makeStyles({
  root: {
    backgroundColor: 'white',
    borderRadius: '5px',
    padding: isMobile ? '20px' : '40px',
  },
  link: {
    textDecoration: 'none',
  },
  cardWrapper: {
    width: '100%',
    justifyContent: 'center',
    display: 'flex',
  },
  photoWrapper: {
    width: isMobile ? '100%' : '80%',
    height: isMobile ? '30vh' : '50vh',
    marginTop: '10px',
  },
  photo: {
    width: '100%',
    height: '100%',
    objectFit: 'cover',
  },
  nameWrapper: {
    color: '#23467A',
    margin: '40px 0 20px 0',
  },
  detailsWrapper: {
    backgroundColor: '#F5F3F3',
    borderRadius: '5px',
    padding: '20px',
  },
  itemTitle: {
    color: '#23467A',
    marginRight: '5px',
  },
  itemValue: {
    color: '#5A5A5A',
  },
  itemWrapper: {
    display: 'flex',
    margin: '5px 0 5px 0',
  },
  textSkeleton: {
    margin: '5px 0 5px 0',
  },
  buttonWrapper: {
    width: '100%',
    marginTop: '20px',
  },
  button: {
    top: '20%',
    left: isMobile ? '30%' : '70%',
    width: isMobile ? '70%' : '30%',
    minHeight: '45px',
    color: 'white',
    fontWeight: 'bold',
    backgroundColor: green[500],
    '&:hover': {
      backgroundColor: green[600],
    },
  },
  iconButton: {
    backgroundColor: '#386FC2',
    '&:hover': {
      backgroundColor: '#386FC2',
    },
    color: 'white',
    height: '40px',
    width: '40px',
  },
});

const Details = (props) => {
  const classes = useStyles();

  const data = props.location.data;

  return !data ? (
    <Redirect to={'/home'} />
  ) : (
    <div className={classes.root}>
      <div className={classes.backWrapper}>
        <Link to="/" className={classes.link}>
          <IconButton className={classes.iconButton}>
            <Back />
          </IconButton>
        </Link>
      </div>
      {data.foto ? (
        <div className={classes.cardWrapper}>
          <Card className={classes.photoWrapper}>
            <CardMedia
              className={classes.photo}
              image={data.foto}
              alt="Touro"
            />
          </Card>
        </div>
      ) : (
        <div />
      )}

      <div className={classes.nameWrapper}>
        <Typography variant="h5">{data.nome}</Typography>
      </div>
      <div className={classes.detailsWrapper}>
        <div className={classes.itemWrapper}>
          <Typography className={classes.itemTitle}>Raça:</Typography>
          <Typography className={classes.itemValue}>{data.raca}</Typography>
        </div>
        <div className={classes.itemWrapper}>
          <Typography className={classes.itemTitle}>
            Centro de Inseminação Atual:
          </Typography>
          <Typography className={classes.itemValue}>{data.centro}</Typography>
        </div>
        <div className={classes.itemWrapper}>
          <Typography className={classes.itemTitle}>RGD:</Typography>
          <Typography className={classes.itemValue}>{data.rgd}</Typography>
        </div>
        <div className={classes.itemWrapper}>
          <Typography className={classes.itemTitle}>
            Número de Filhas:
          </Typography>
          <Typography className={classes.itemValue}>
            {data.numFilhas}
          </Typography>
        </div>
        <div className={classes.itemWrapper}>
          <Typography className={classes.itemTitle}>
            Número de Rebanhos:
          </Typography>
          <Typography className={classes.itemValue}>
            {data.numRebanhos}
          </Typography>
        </div>
        <div className={classes.itemWrapper}>
          <Typography className={classes.itemTitle}>PTA Leite:</Typography>
          <Typography className={classes.itemValue}>{data.ptaLeite}</Typography>
        </div>
        <div className={classes.itemWrapper}>
          <Typography className={classes.itemTitle}>PTA Leite Conf:</Typography>
          <Typography className={classes.itemValue}>
            {data.ptaLeiteConf}
          </Typography>
        </div>
        <div className={classes.itemWrapper}>
          <Typography className={classes.itemTitle}>PTA IPP:</Typography>
          <Typography className={classes.itemValue}>{data.ptaIpp}</Typography>
        </div>
        <div className={classes.itemWrapper}>
          <Typography className={classes.itemTitle}>PTA IPP Conf:</Typography>
          <Typography className={classes.itemValue}>
            {data.ptaIppConf}
          </Typography>
        </div>
        <div className={classes.itemWrapper}>
          <Typography className={classes.itemTitle}>Beta Caseína:</Typography>
          <Typography className={classes.itemValue}>
            {data.betaCaseina}
          </Typography>
        </div>
      </div>
      <div className={classes.buttonWrapper}>
        <Button startIcon={<Message />} className={classes.button}>
          Entre em Contato
        </Button>
      </div>
    </div>
  );
};

export default Details;
