import React, { useEffect, useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import {
  Container,
  Card,
  CardContent,
  CardMedia,
  Typography,
  Fab,
  ButtonBase,
} from '@material-ui/core';
import { green } from '@material-ui/core/colors';
import Skeleton from '@material-ui/lab/Skeleton';
import SearchRoundedIcon from '@material-ui/icons/SearchRounded';
import Banner from '../../assets/home-banner.jpg';
import { Link } from 'react-router-dom';
import { isMobile } from 'react-device-detect';
import { db } from '../../db/firebase';

const useStyles = makeStyles({
  root: {
    backgroundColor: 'white',
    borderRadius: '5px',
  },
  link: {
    textDecoration: 'none',
  },
  container: {
    width: '90%',
    padding: '30px 0 30px 0',
  },
  banner: {
    marginBottom: '30px',
  },
  bannerMedia: {
    width: 'auto',
    height: isMobile ? '200px' : '385px',
  },
  labelPrimary: {
    color: '#23467A',
    marginBottom: '10px',
  },
  labelSecondary: {
    color: '#5A5A5A',
    marginBottom: '5px',
  },
  itemCard: {
    padding: '15px',
    margin: '10px',
    width: '100%',
    display: isMobile ? '' : 'flex',
    maxHeight: isMobile ? '' : '150px',
  },
  itemFlex: {
    flex: 1,
  },
  itemImg: {
    width: '90%',
    maxHeight: '100%',
    borderRadius: '5px',
    marginRight: '10px',
  },
  itemContent: {
    flex: 2,
    display: 'grid',
    textAlign: 'initial',
  },
  fab: {
    position: 'fixed',
    bottom: isMobile ? '2%' : '3%',
    right: isMobile ? '10%' : '28%',
    color: 'white',
    fontWeight: 'bold',
    backgroundColor: green[500],
    '&:hover': {
      backgroundColor: green[600],
    },
  },
  buttonBase: {
    width: '100%',
  },
});

const Home = (props) => {
  const [touros, setTouros] = useState([]);
  const classes = useStyles();

  const query = props.location ? props.location.data : null;

  useEffect(() => {
    db.collection('touros1')
      .get()
      .then((docs) => {
        if (docs) {
          let data = [];

          docs.forEach((item) => data.push({ ...item.data() }));

          if (query) {
            data.sort((a, b) => {
              if (a[query.c1] > b[query.c1]) return 1;
              else if (a[query.c1] < b[query.c1]) return -1;
              else return 0;
            });
          }

          setTouros(data);
        }
      });
  }, [query]);

  return (
    <div className={classes.root}>
      <Container className={classes.container}>
        <Card className={classes.banner}>
          <CardMedia
            className={classes.bannerMedia}
            image={Banner}
            title="Alta Genetics Banner"
          />
          <CardContent>Alta Genetics Banner</CardContent>
        </Card>
        <Typography variant="h5" className={classes.labelPrimary}>
          Últimos animais consultados e favoritos:
        </Typography>
        <div>
          {touros.map((data, index) => {
            return (
              <Link
                key={index}
                to={{ pathname: '/details', data: { ...data } }}
                className={classes.link}
              >
                <ButtonBase className={classes.buttonBase}>
                  <Card className={classes.itemCard}>
                    <div className={classes.itemFlex}>
                      {data.foto ? (
                        <img
                          className={classes.itemImg}
                          src={data.foto}
                          alt="Touro"
                        />
                      ) : isMobile ? (
                        <Skeleton variant="rect" width={'100%'} height={100} />
                      ) : (
                        <Skeleton
                          variant="rect"
                          width={'90%'}
                          height={'100%'}
                        />
                      )}
                    </div>
                    <div className={classes.itemContent}>
                      <Typography variant="h6" className={classes.labelPrimary}>
                        {data.nome}
                      </Typography>
                      <Typography
                        className={classes.labelSecondary}
                      >{`Raça: ${data.raca}`}</Typography>
                      <Typography
                        className={classes.labelSecondary}
                      >{`Centro: ${data.centro}`}</Typography>
                      <Typography
                        className={classes.labelSecondary}
                      >{`Beta Caseína: ${data.betaCaseina}`}</Typography>
                    </div>
                  </Card>
                </ButtonBase>
              </Link>
            );
          })}
        </div>
        <Link to="/search" className={classes.link}>
          <Fab className={classes.fab} variant="extended">
            <SearchRoundedIcon />
            {isMobile ? null : 'Buscar'}
          </Fab>
        </Link>
      </Container>
    </div>
  );
};

export default Home;
