import React, { useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import {
  AppBar,
  Toolbar,
  Typography,
  IconButton,
  ButtonBase,
  Menu,
  MenuItem,
} from '@material-ui/core';
import MenuRoundedIcon from '@material-ui/icons/MenuRounded';
import { Link } from 'react-router-dom';
import { firebaseAuth } from '../../db/firebase';

const useStyles = makeStyles({
  appbar: {
    backgroundColor: '#386FC2',
    width: '100%',
  },
  link: {
    textDecoration: 'none',
  },
  logoWrapper: {
    flexGrow: 1,
  },
  toolbar: {
    minHeight: '45px',
  },
  logo: {
    color: 'white',
    paddingLeft: '30px',
    fontSize: '28px',
  },
});

const HeaderMenu = () => {
  const [anchorEl, setAnchorEl] = useState(null);
  const open = Boolean(anchorEl);

  const classes = useStyles();

  const handleMenu = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  return (
    <AppBar position="static" className={classes.appbar}>
      <Toolbar className={classes.toolbar}>
        <div className={classes.logoWrapper}>
          <Link to="/" className={classes.link}>
            <ButtonBase>
              <Typography className={classes.logo}>InBovis</Typography>
            </ButtonBase>
          </Link>
        </div>
        <IconButton edge="end" color="inherit" onClick={handleMenu}>
          <MenuRoundedIcon />
        </IconButton>
        <Menu
          anchorEl={anchorEl}
          anchorOrigin={{
            vertical: 'top',
            horizontal: 'right',
          }}
          keepMounted
          transformOrigin={{
            vertical: 'top',
            horizontal: 'right',
          }}
          open={open}
          onClose={handleClose}
        >
          <MenuItem onClick={handleClose}>Contato</MenuItem>
          <MenuItem onClick={handleClose}>Configurações</MenuItem>
          <MenuItem onClick={() => firebaseAuth.signOut()}>Sair</MenuItem>
        </Menu>
      </Toolbar>
    </AppBar>
  );
};

export default HeaderMenu;
