import React, { useState } from 'react';
import {
  Button,
  InputLabel,
  Input,
  IconButton,
  InputAdornment,
  TextField,
  FormControl,
} from '@material-ui/core';
import { green } from '@material-ui/core/colors';
import { Visibility, VisibilityOff } from '@material-ui/icons';
import { makeStyles } from '@material-ui/core/styles';
import MuiAlert from '@material-ui/lab/Alert';
import { firebaseAuth } from '../../db/firebase';

const useStyles = makeStyles({
  inputsContainer: {
    display: 'flex',
    flexDirection: 'column',
    margin: '25px',
  },
  inputs: {
    marginTop: '15px',
  },
  button: {
    flex: 1,
    marginTop: '15px',
    color: 'white',
    fontWeight: 'bold',
    backgroundColor: green[500],
    '&:hover': {
      backgroundColor: green[600],
    },
  },
  icon: {
    height: '27px',
    width: '27px',
  },
  alertDiv: {
    marginTop: '10px',
  },
});

const SignUp = ({ onPageChange }) => {
  const classes = useStyles();
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [password2, setPassword2] = useState('');
  const [showPassword, setShowPassword] = useState(false);
  const [showPassword2, setShowPassword2] = useState(false);
  const [warningMessage, setWarningMessage] = useState('');

  const handleClickShowPassword = () => {
    setShowPassword(!showPassword);
  };

  const handleClickShowPassword2 = () => {
    setShowPassword2(!showPassword2);
  };

  const handleMouseDownPassword = (event) => {
    event.preventDefault();
  };

  const handleSubmitUser = () => {
    if (username === '' || password === '') {
      setWarningMessage('Preencha todos os campos!');
    } else if (password !== password2) {
      setWarningMessage('Senhas são diferentes!');
    } else {
      firebaseAuth.createUserWithEmailAndPassword(username, password);
      onPageChange('Login');
    }
  };

  return (
    <div className={classes.inputsContainer}>
      <TextField
        value={username}
        onChange={(event) => setUsername(event.target.value)}
        className={classes.inputs}
        label="Insira seu e-mail"
      />
      <FormControl className={classes.inputs}>
        <InputLabel htmlFor="password">Insira sua senha</InputLabel>
        <Input
          id="password"
          type={showPassword ? 'text' : 'password'}
          value={password}
          onChange={(e) => setPassword(e.target.value)}
          endAdornment={
            <InputAdornment position="end">
              <IconButton
                aria-label="toggle password visibility"
                onClick={handleClickShowPassword}
                onMouseDown={handleMouseDownPassword}
              >
                {showPassword ? <Visibility /> : <VisibilityOff />}
              </IconButton>
            </InputAdornment>
          }
        />
      </FormControl>
      <FormControl className={classes.inputs}>
        <InputLabel htmlFor="password">Confirme sua senha</InputLabel>
        <Input
          id="password2"
          type={showPassword2 ? 'text' : 'password'}
          value={password2}
          onChange={(e) => setPassword2(e.target.value)}
          endAdornment={
            <InputAdornment position="end">
              <IconButton
                aria-label="toggle password visibility"
                onClick={handleClickShowPassword2}
                onMouseDown={handleMouseDownPassword}
              >
                {showPassword2 ? <Visibility /> : <VisibilityOff />}
              </IconButton>
            </InputAdornment>
          }
        />
      </FormControl>
      {warningMessage !== '' && (
        <MuiAlert
          className={classes.alertDiv}
          elevation={6}
          variant="filled"
          severity="error"
        >
          {warningMessage}
        </MuiAlert>
      )}
      <Button className={classes.button} onClick={handleSubmitUser}>
        Cadastrar
      </Button>
      <Button
        className={classes.button}
        onClick={() => {
          onPageChange('Login');
        }}
      >
        Voltar
      </Button>
    </div>
  );
};

export default SignUp;
