import React, { useEffect, useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { Typography, TextField, Button } from '@material-ui/core';
import { green } from '@material-ui/core/colors';
import Autocomplete from '@material-ui/lab/Autocomplete';
import { Link } from 'react-router-dom';
import { isMobile } from 'react-device-detect';
import { db } from '../../db/firebase';

const useStyles = makeStyles({
  root: {
    backgroundColor: 'white',
    borderRadius: '5px',
  },
  link: {
    textDecoration: 'none',
  },
  headerWrapper: {
    padding: isMobile ? '20px' : '50px',
  },
  label: {
    color: '#23467A',
  },
  labelWrapper: {
    margin: '20px 0 20px 0',
  },
  comboBox: {
    width: isMobile ? '95%' : '60%',
  },
  optionWrapper: {
    padding: `10px 0 0 ${isMobile ? '20px' : '50px'}`,
  },
  buttonWrapper: {
    width: isMobile ? '100%' : '80%',
    padding: isMobile ? '20px' : '70px',
  },
  button: {
    width: isMobile ? '90%' : '35%',
    minHeight: '40px',
    color: 'white',
    fontWeight: 'bold',
    backgroundColor: green[500],
    '&:hover': {
      backgroundColor: green[600],
    },
  },
});

const Search = () => {
  const classes = useStyles();
  const [options, setOptions] = useState([]);
  const [option1, setOption1] = useState('');
  const [option2, setOption2] = useState('');
  const [option3, setOption3] = useState('');

  useEffect(() => {
    db.collection('dados')
      .doc('caracteristicas')
      .get()
      .then((doc) => {
        if (doc) {
          let data = [];

          Object.entries(doc.data()).forEach((item) => data.push(item));

          setOptions(data);
        }
      });
  }, []);

  return (
    <div className={classes.root}>
      <div className={classes.headerWrapper}>
        <Typography className={classes.label} variant="h5">
          Selecione as principais características desejadas:
        </Typography>
      </div>
      <div className={classes.optionWrapper}>
        <div className={classes.labelWrapper}>
          <Typography className={classes.label} variant="h6">
            Característica 1:
          </Typography>
        </div>
        <Autocomplete
          value={option1}
          onChange={(e, value) => setOption1(value)}
          className={classes.comboBox}
          options={options}
          getOptionLabel={(option) => option[1] || ''}
          renderInput={(params) => (
            <TextField {...params} label="Selecione..." variant="outlined" />
          )}
        />
      </div>
      <div className={classes.optionWrapper}>
        <div className={classes.labelWrapper}>
          <Typography className={classes.label} variant="h6">
            Característica 2:
          </Typography>
        </div>
        <Autocomplete
          value={option2}
          onChange={(e, value) => setOption2(value)}
          className={classes.comboBox}
          options={options}
          getOptionLabel={(option) => option[1] || ''}
          renderInput={(params) => (
            <TextField {...params} label="Selecione..." variant="outlined" />
          )}
        />
      </div>
      <div className={classes.optionWrapper}>
        <div className={classes.labelWrapper}>
          <Typography className={classes.label} variant="h6">
            Característica 3:
          </Typography>
        </div>
        <Autocomplete
          value={option3}
          onChange={(e, value) => setOption3(value)}
          className={classes.comboBox}
          options={options}
          getOptionLabel={(option) => option[1] || ''}
          renderInput={(params) => (
            <TextField {...params} label="Selecione..." variant="outlined" />
          )}
        />
      </div>
      <div className={classes.buttonWrapper}>
        <Link
          to={{
            pathname: '/home',
            data: { c1: option1[0], c2: option2[0], c3: option3[0] },
          }}
          className={classes.link}
        >
          <Button className={classes.button}>Buscar Melhores Opções</Button>
        </Link>
      </div>
    </div>
  );
};

export default Search;
